import './App.css';
import Navbar from "./components/Navbar";
import { useState } from 'react';
import NewsContainer from './components/NewsContent/NewsContainer';
import axios from "axios";
import { useEffect } from 'react';
import Footer from './components/Footercomponents/Footer';
import {Route,Switch} from "react-router-dom";



function App() {
 
  
  let s;
  var [category,setCategory] = useState("general");
  var [newsArray,setNewsArray]= useState([]);
  var [newsResult,setNewsResults] = useState();
  var [loadMore,setLoadMore] = useState(20)
  
  s = category.charAt(0).toLowerCase()+category.slice(1);
 

  let  apikey  =  `https://saurav.tech/NewsAPI/top-headlines/category/${s}/in.json`;;
 
  
 
 
  
  const newsapi = async() =>{
        try {
          const news = await axios.get(apikey);
          document.querySelector("#marquee").innerHTML = "";
         
          setNewsArray(news.data.articles);
          setNewsResults(news.data.totalResults);

          
        } catch (error) {
          console.log(error);
        } 
     
  }
 
  useEffect(() => {
   
    newsapi(); 
  }, [newsResult,category]); 
 
 
 
 

  


  
  
 
  
    
 
  
 
  
  return (
    <div className="App">
         <Navbar setCategory = {setCategory}/>
         <NewsContainer newsArray = {newsArray} newsResult = {newsResult} loadMore = {loadMore}  setLoadMore = {setLoadMore}/>
         
        
       
        
         <Footer/>
         
         
         
         
    </div>
  );
}

export default App;
