import React from 'react'
import "./NewsCard.css";

const NewsCard = ({ newsItem }) => {
  
    const fulldate = new Date(newsItem.publishedAt);
    
    var date = fulldate.toString().split(" ");
    const hour = parseInt(date[4].substring(0,2));
    const time = hour>12?true:false;


    return (
        
        <div className="newscard">
            <img src={newsItem.urlToImage ? newsItem.urlToImage : "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcR52ox5Lid5dIMde3wQaIQf1E_vMcV2HOy8-6eYDTRex_0JmHZz5gnny04rTVayyuJrHto&usqp=CAU"} alt="" className="newsImage" />
            <div className="newsText">
                <div>
                    <strong>Title:  </strong> 
                    <span className="title">
                      {newsItem.title}.
                    </span>
                   
                   
                </div>
                <div className="lowernewstext">
                    


                    <div >
                       <strong>Description: </strong>  <span className = "description">{newsItem.description}</span> 
                    </div>
                   
                   

                    <div className = "readmore">
                    <div className = "author">
                    <a href={newsItem.url} target="__blank"><strong> Source: </strong></a>

                    <span className = "muted">{newsItem.author?newsItem.author:"unknown"} |{" "}
                    {time? `${hour-12}:${date[4].substring(3,5)} pm`:`${hour}:${date[4].substring(3,5)} am`} on {date[2]} {date[1]} {date[3]}, {date[0]}.
                   
                    </span>      
                    </div>     
                    <strong style = {{color:"black"}}>Read more here: </strong>
                        <a href = {newsItem.url} target = "__blank">
                          {newsItem.source.name}
                        </a>

                    </div>

                </div>

            </div>

        </div>

    )

}

export default NewsCard

